//Metropolis Font Version that we use: r8 https://github.com/chrismsimpson/Metropolis/releases/tag/r8

@if $clr-fontSkipBase64 == false {
  @font-face {
    font-family: 'Metropolis';
    src: url(data:application/font-woff;charset=utf-8;base64,)
      format('woff');
    font-weight: 200;
    font-style: normal;
  }

  @font-face {
    font-family: 'Metropolis';
    src: url(data:application/font-woff;charset=utf-8;base64,)
      format('woff');
    font-weight: 400;
    font-style: normal;
  }

  @font-face {
    font-family: 'Metropolis';
    // medium
    src: url(data:application/font-woff;charset=utf-8;base64,)
      format('woff');
    font-weight: 500;
    font-style: normal;
  }

  @font-face {
    font-family: 'Metropolis';
    // semibold
    src: url(data:application/font-woff;charset=utf-8;base64,)
      format('woff');
    font-weight: 600;
    font-style: normal;
  }
}
